// @flow
import React from 'react';
import type { Element } from 'react';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import ContingenciesTable from './ContingenciesTable';
import { mappingOfferContingencies, isOfferContingencies } from 'helpers/Offer';
import type { Offer } from 'types/Offer';

export const USER_ROLES = {
  BUYER: 'buyer',
  SELLER: 'seller',
};

export const getRole = (user: Object): string => {
  const isBuyer = _get(user, 'IsBuyer', false);
  const isSeller = _get(user, 'IsSeller', false);

  if (isSeller) return USER_ROLES.SELLER;
  if (isBuyer) return USER_ROLES.BUYER;
  return '';
};

export const displayContingencies = (
  offers: Array<Offer>,
): Array<Element<Object>> => {
  return offers.reduce((acc, offer) => {
    const isValid =
      isOfferContingencies(offer) && !_isEmpty(_get(offer, ['Contingencies']));

    if (!isValid) return acc;

    const contingencies = mappingOfferContingencies(offer);
    return [
      ...acc,
      <ContingenciesTable
        key={_get(offer, ['OfferId'])}
        offer={offer}
        contingencies={contingencies}
      />,
    ];
  }, []);
};

export const mapSellerData = (properties: Array<Object>): Array<Offer> => {
  return properties.reduce((list, current) => {
    const originalOffers = _get(current, 'Offers') || [];
    const offers = originalOffers.map(offer => ({
      ...offer,
      Property: { PropertyData: _get(current, 'PropertyData') },
    }));
    return [...list, ...offers];
  }, []);
};
