// @flow
import React, { Fragment } from 'react';
import type { Element } from 'react';
import { Widget } from 'components/Widget';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import _isEmpty from 'lodash/isEmpty';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core';
import type { Offer } from 'types/Offer';
import _get from 'lodash/get';

const TableHeaderCell = withStyles(theme => ({
  root: {
    fontWeight: 'bold',
  },
}))(TableCell);

const useStyles = makeStyles(theme => ({
  widget: {
    marginBottom: theme.spacing(3),
  },
  description: {
    ...theme.typography.h5,
    marginBottom: theme.spacing(3),
    color: theme.palette.primary.gray,
  },
  table: {
    fontFamily: theme.typography.fontFamily.avenirLT,
  },
  cell: {
    height: theme.spacing(6),
    [theme.breakpoints.up('md')]: {
      border: 'none',
    },
  },
}));

type Props = {
  contingencies: Object[],
  offer: Offer,
};

const ContingenciesTable = ({
  contingencies,
  offer,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();

  if (_isEmpty(contingencies) || _isEmpty(offer)) return <></>;

  return (
    <Widget className={classes.widget} title="Contingencies status">
      <div className={classes.description}>
        Contingencies status of your property <strong>{_get(offer, 'Property.PropertyData.FullAddress', '')}</strong>
      </div>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableHeaderCell padding="none">Type</TableHeaderCell>
            <TableHeaderCell padding="none">Status</TableHeaderCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {contingencies.map(contingency => {
            return (
              <TableRow key={contingency.title}>
                <TableCell className={classes.cell} padding="none">
                  {contingency.title}
                </TableCell>
                <TableCell className={classes.cell} padding="none">
                  {contingency.status || 'N/A'}
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    </Widget>
  );
};
export default ContingenciesTable;
