// @flow
import React, { useContext, Fragment } from 'react';
import type { Element } from 'react';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _isEmpty from 'lodash/isEmpty';
import type { Offer as OfferType } from 'types/Offer';
import { withErrorBoundary } from 'components/ErrorBoundary';
import UserContext from 'contexts/UserContext';
import {
  USER_ROLES,
  getRole,
  mapSellerData,
  displayContingencies,
} from './helper';

const useStyles = makeStyles(theme => ({
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
    minHeight: '100px',
  },
}));

type Props = {
  offers: Array<OfferType>,
  isLoading: Boolean,
};

const Contingencies = ({
  offers,
  isLoading,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();
  const user = useContext(UserContext);
  const role = getRole(user);
  const isBuyer = role === USER_ROLES.BUYER;

  if (_isEmpty(offers) || !role) return <></>;

  const offerList = isBuyer ? offers : mapSellerData(offers);

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx({
        [classes.loading]: isLoading,
      })}
    >
      {displayContingencies(offerList)}
    </BlockUi>
  );
};

export default (withErrorBoundary(Contingencies): Element<'div'>);
